$color-basic-white: #fff;
$color-basic-beige: #f9f5f2;
$color-basic-black: #202322;
$color-basic-accent: #6b360d;

$color-elements: (
    'air'  : #de64d2,
    'fire' : #ea5c3d,
    'water': #2992de,
    'earth': #559d3e
);

:root {
    --color-basic-white    : #{$color-basic-white};
    --color-basic-beige    : #{$color-basic-beige};
    --color-basic-black    : #{$color-basic-black};
    --color-basic-accent   : #{$color-basic-accent};
    --color-basic-greener  : #d7dd93;
    --color-basic-pink     : #f6c0b1;
    --color-basic-turquoise: #b6d1c5;
    --color-basic-yellow   : #fece30;

    --color-elements-air   : #{map-get($color-elements, 'air')};
    --color-elements-fire  : #{map-get($color-elements, 'fire')};
    --color-elements-water : #{map-get($color-elements, 'water')};
    --color-elements-earth : #{map-get($color-elements, 'earth')};
}
